
import Vue from "vue";
import { TextFilter } from "../../../lib/entities";
import { createUniqueId, findNewName } from "../../../lib/utils";

export default Vue.extend({
    props: {
        filters: { type: Array, default: () => [] },
        categoryName: { type: String, default: () => "" },
    },
    computed: {
        textFilters: function () {
            return this.filters.filter((filter) => filter instanceof TextFilter);
        },
    },
    methods: {
        addFilter() {
            const categoryName = this.$props.categoryName;

            const existingFilters = (this as any).$root.$data.categories.getFilters(categoryName);
            const newFilter = new TextFilter(
                createUniqueId(),
                findNewName(
                    "New filter",
                    existingFilters
                        .filter((filter: any) => filter instanceof TextFilter)
                        .map((filter: TextFilter) => filter.displayName),
                ),
                "wildcard",
                {
                    contraAccount: "",
                    description: "",
                },
            );

            this.$root.$data.categories.addFilters(categoryName, [newFilter]);
            (this as any).openWindow("text-filter-edit", {
                categoryName: categoryName,
                filterId: newFilter.id,
            });
        },

        openFilter(filterId: number) {
            (this as any).openWindow("text-filter-edit", {
                categoryName: this.$props.categoryName,
                filterId: filterId,
            });
        },

        removeFilter(filterId: number) {
            this.$root.$data.categories.removeFilters(this.$props.categoryName, [filterId]);
        },

        transactionsInFilter: function (filterId: number) {
            return this.$root.$data.searcher.searchTransactions({
                categoryName: this.$props.categoryName,
                filterId: filterId,
            }).length as number;
        },
    },
});
