
import { AssignedTransaction } from "../../../lib/entities";
import Vue from "vue";

export default Vue.extend({
    data: () => {
        return {
            selectedTransactionHashes: [],
        };
    },
    computed: {
        allTransactions() {
            return this.$root.$data.assigner
                .allTransactions()
                .filter((transaction: AssignedTransaction) => transaction.assignments.length === 0);
        },
        allCategories() {
            return this.$root.$data.categories.list();
        },
    },
});
