export default "/* Customize style */\n\n:root {\n    --color-background: #1f1f1f;\n    --color-background-nav: #222222;\n    --color-background-nav-selected: #1d1d1d;\n    --color-background-button: #2f2f2f;\n    --color-background-button-dark: #282828;\n    --color-background-button-light: #3f3f3f;\n    --color-background-light-hover: #646665;\n    --color-border-nav: #2a2a2a;\n    --color-fill-icon: #a6a6a6;\n    --color-primary: #dee4e8;\n    --color-primary-selected: #ffffff;\n    --color-primary-deselected: #aaaaaa;\n    --color-theme: #fa5b0f;\n    --color-theme-light: #ff7729;\n    --color-theme-positive: #33cc00;\n    --color-theme-negative: #e96a32;\n}\n\nhtml {\n    font-size: 0.55rem;\n}\n\nhtml,\nbody {\n    background-color: var(--color-background);\n    color: var(--color-primary);\n    overflow: hidden; /* Hide scrollbars */\n}\n\na {\n    color: var(--color-theme);\n}\n\n.button {\n    background-color: var(--color-background-button);\n    color: var(--color-primary);\n    border-color: var(--color-primary-deselected);\n    border-radius: 0.4rem;\n    border-width: 0.15rem;\n    font-size: 1.8rem;\n    line-height: 2.7rem;\n    padding: 0rem 1rem;\n    margin-bottom: 0rem;\n    font-weight: initial;\n    text-transform: none;\n}\n\n.button:hover {\n    background-color: var(--color-background-light);\n    border-color: var(--color-background-light-hover);\n}\n\n.button-light {\n    background-color: var(--color-background-button-light);\n}\n\n.button-dark {\n    background-color: var(--color-background-button-dark);\n}\n\n/* Components */\n\n.app {\n    display: flex;\n    flex-flow: column;\n}\n\n.view {\n    z-index: 0;\n    flex: 1 1 auto;\n    position: relative;\n}\n\n.view-width {\n    min-width: 32rem;\n    width: 90%;\n    max-width: 60rem;\n    margin: 0 auto;\n}\n\n.nav {\n    z-index: 1;\n    flex: 0 1 6rem;\n    background-color: var(--color-background-nav);\n    box-shadow: 0 0rem 0.2rem black;\n    border-top: 0.1rem solid var(--color-border-nav);\n\n    display: flex;\n    justify-content: center;\n}\n\n@media screen and not (min-height: 15.8rem) {\n    .nav {\n        display: none;\n    }\n}\n\n.nav .nav-button {\n    width: 7rem;\n    height: calc(100% - 0.1rem);\n    margin: 0.05rem 0.7rem;\n    position: relative;\n    cursor: pointer;\n    -webkit-tap-highlight-color: transparent;\n\n    border-radius: 1rem;\n}\n\n.nav .nav-button span {\n    position: absolute;\n    left: 0;\n    top: 3.33rem;\n    width: 100%;\n    font-size: 1.4rem;\n    text-align: center;\n    user-select: none;\n    color: var(--color-primary-deselected);\n    transition: color 0.2s;\n}\n\n.nav .nav-button svg {\n    position: absolute;\n    left: 1.75rem;\n    top: 0.43rem;\n    width: 3.5rem;\n    height: 3.5rem;\n    fill: var(--color-fill-icon);\n    transition: filter 0.2s;\n}\n\n.nav .nav-button.selected svg,\n.nav .nav-button:hover svg {\n    filter: brightness(150%);\n}\n\n.nav .nav-button.selected span,\n.nav .nav-button:hover span {\n    color: var(--color-primary-selected);\n}\n\n.nav .nav-button.selected {\n    background-color: var(--color-background-nav-selected);\n}\n\n.top-nav {\n    z-index: 1;\n    flex: 0 1 5rem;\n    background-color: var(--color-background-nav);\n    box-shadow: 0 0rem 0.2rem black;\n    border-bottom: 0.1rem solid var(--color-border-nav);\n}\n\n.top-nav .top-nav-button,\n.top-nav .top-nav-title,\n.sub-nav-button {\n    margin: 0.4rem 1.05rem 0.3rem;\n    font-size: 2.1rem;\n    user-select: none;\n    -webkit-tap-highlight-color: transparent;\n    color: var(--color-primary-deselected);\n    padding: 0.1rem 1rem 0.3rem;\n\n    border-radius: 1rem;\n    border: none;\n}\n\n.top-nav .top-nav-button,\n.sub-nav-button {\n    cursor: pointer;\n    transition: color 0.2s;\n}\n\n.top-nav .top-nav-button:hover,\n.sub-nav-button:hover {\n    color: var(--color-primary-selected);\n}\n\n.top-nav .top-nav-button.selected,\n.sub-nav-button.selected {\n    color: var(--color-primary-selected);\n    background-color: var(--color-background-nav-selected);\n}\n\n.sub-nav-button.selected {\n    background-color: var(--color-background-nav);\n}\n\n.top-nav-button.button {\n    border: 0.15rem solid var(--color-background-light-hover);\n    border-radius: 0.4rem;\n}\n\n.top-nav .top-nav-title {\n    color: var(--color-primary);\n    margin-top: 0.6rem;\n}\n\n.top-nav .top-nav-edge {\n    width: 9rem;\n}\n\n.sub-nav-button {\n    font-size: 1.7rem;\n    background: var(--color-background-button-dark);\n}\n\n.tab-splitter {\n    flex: 0 0 0.3rem;\n    margin-left: -150rem;\n    width: 360rem;\n    background-color: var(--color-primary-deselected);\n}\n\n.tab-heading {\n    flex: 0 0 1rem;\n    display: block;\n    height: 4rem;\n    width: 100%;\n    font-size: 2rem;\n    text-align: center;\n}\n\n.sub-tab-heading {\n    display: block;\n    height: 2.5rem;\n    font-size: 1.5rem;\n    text-align: center;\n}\n\n.expand-button {\n    position: absolute;\n    right: 2px;\n    top: 0px;\n    transform: scale(0.6);\n}\n\n/* Home view */\n\n.home-title {\n    width: 100%;\n    text-align: center;\n    margin-top: 4rem;\n}\n\n/* Sources tab */\n\n.tab-contents {\n    position: absolute;\n    left: 0rem;\n    right: 0rem;\n    top: 0rem;\n    bottom: 0rem;\n    display: flex;\n    flex-flow: column;\n}\n\n.tab-contents span {\n    user-select: none;\n}\n\n.sources-summary {\n    display: flex;\n    width: 100%;\n    height: 8.5rem;\n    margin-top: 1rem;\n    justify-content: center;\n}\n\n.sources-summary-icon {\n    fill: var(--color-fill-icon);\n    width: 6.5rem;\n    height: 6.5rem;\n    vertical-align: bottom;\n}\n\n.sources-summary-separator {\n    width: 2rem;\n}\n\n.sources-summary-stat {\n    font-size: 3.5rem;\n    line-height: 6.5rem;\n}\n\n.sources-summary-stat-wide {\n    min-width: 11.5rem;\n    max-width: 11.5rem;\n    text-align: right;\n    display: inline-block;\n}\n\n.sources-summary-label {\n    float: right;\n    font-size: 1.5rem;\n    line-heig