ht: 5rem;\n    right: 0rem;\n    bottom: 0.5rem;\n}\n\n.remove-button:hover {\n    filter: brightness(150%);\n}\n\n.source-data-name {\n    position: absolute;\n    left: 1rem;\n    top: 0.4rem;\n    font-size: 1.8rem;\n    width: calc(100% - 5.8rem);\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.source-data-date {\n    position: absolute;\n    left: 0.9rem;\n    bottom: 0.7rem;\n    font-size: 1.2rem;\n}\n\n.source-data-accounts {\n    right: 12.3rem;\n    bottom: 0.7rem;\n}\n\n.source-data-accounts-icon {\n    position: absolute;\n    right: 10.6rem;\n    width: 1.7rem;\n    height: 1.7rem;\n    bottom: 0.8rem;\n    fill: var(--color-fill-icon);\n}\n\n.source-data-transactions {\n    right: 6.8rem;\n    bottom: 0.7rem;\n}\n\n.source-data-transactions-icon {\n    position: absolute;\n    right: 4.9rem;\n    width: 1.8rem;\n    height: 1.8rem;\n    bottom: 0.8rem;\n    fill: var(--color-fill-icon);\n}\n\n.sources-entry {\n    margin-bottom: 1.2rem;\n}\n\n.sources-entry .button {\n    margin-bottom: 0rem;\n    max-width: 15rem;\n    position: relative;\n    font-size: 1.7rem;\n    padding-bottom: 0.2rem;\n    white-space: nowrap;\n}\n\n.sources-entry label {\n    line-height: 3.5rem;\n    user-select: none;\n}\n.sources-entry-open-button {\n    margin-left: 1.2rem;\n    margin-right: 1.2rem;\n}\n\n.preferences-icon {\n    position: absolute;\n    right: 1.5rem;\n    top: 0.625rem;\n    width: 3.75rem;\n    height: 3.75rem;\n    padding: 0.5rem;\n    overflow: visible;\n    fill: var(--color-fill-icon);\n}\n\n/* Categories tab */\n\n.transaction-date-splitter {\n    display: block;\n    width: 100%;\n    text-align: center;\n}\n\n.transaction-amount {\n    left: -0.5rem;\n    top: 0.45rem;\n    font-size: 2.2rem;\n    width: 13rem;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    text-align: right;\n}\n\n.transaction-direction-up,\n.transaction-direction-down {\n    left: 13rem;\n\n    width: 0;\n    height: 0;\n    border-left: 0.5rem solid transparent;\n    border-right: 0.5rem solid transparent;\n}\n\n.transaction-direction-up {\n    top: 2rem;\n    border-bottom: 0.7rem solid var(--color-theme-positive);\n}\n\n.transaction-direction-down {\n    top: 2.1rem;\n    border-top: 0.7rem solid var(--color-theme-negative);\n}\n\n.transaction-title,\n.transaction-description {\n    right: 0.5rem;\n    width: calc(100% - 15.5rem);\n    text-align: center;\n    text-transform: none;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    overflow: hidden;\n}\n\n.transaction-title {\n    top: 0.1rem;\n    font-size: 1.7rem;\n}\n\n.transaction-description {\n    bottom: 0.4rem;\n    font-size: 1.2rem;\n    color: var(--color-primary-deselected);\n}\n\n.transaction-group > .collection-item {\n    height: 5.2rem;\n}\n\n.collection-grid-container {\n    flex: 0 0 40%;\n    max-height: 30rem;\n    width: 100%;\n    overflow-y: auto;\n}\n\n.collection-grid {\n    width: 100%;\n    margin: auto;\n    text-align: center;\n    font-size: 0; /* Remove any accidental whitespace between items */\n}\n\n.grid-item {\n    display: inline-block;\n    vertical-align: top;\n    border: 0.15rem solid var(--color-primary-deselected);\n    width: calc(33% - 1.2rem);\n    height: 7rem;\n    margin: 0.6rem;\n}\n\n.category-name {\n    display: flex;\n    width: 100%;\n    height: 100%;\n    font-size: 1.5rem;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n    white-space: normal;\n    overflow: hidden;\n}\n\n/* Budget tab */\n\n.budget-entry {\n    position: relative;\n    width: 9rem;\n    height: 100%;\n    margin-right: 1.05rem;\n}\n\n.budget-entry input {\n    position: absolute;\n    top: 0.6rem;\n    width: 100%;\n    font-size: 1.5rem;\n    height: 2.2rem;\n    padding: 0rem;\n}\n\n.budget-entry span {\n    position: absolute;\n    bottom: 0.3rem;\n    width: 100%;\n    text-align: center;\n    font-size: 1.15rem;\n    user-select: none;\n    white-space: nowrap;\n}\n\n.progress-bar {\n    position: relative;\n    flex: 0 0 1rem;\n    height: 1rem;\n    width: 100%;\n    border-radius: 0.48rem;\n    background-color: var(--color-background-button-light);\n    overflow: hidden;\n}\n\n.progress-bar-success-line,\n.progress-bar-overflow-line {\n    position: absolute;\n    height: 100%;\n    left: 0rem;\n    transition: width 0.3s;\n    border-top-right-radius: 0.48rem;\n    border-bottom-right-radius: 0.48rem;\n}\n\n.progress-bar-success-line {\n    background-color: var(--color-theme-positive);\n    z-index: 2;\n}\n\n.progress-bar-overflow-line {\n    background-color: var(--color-theme-negative);\n    z-index: 1;\n}\n\n.budget-results {\n    position: relative;\n}\n\n.budget-results-transactions {\n    position: absolute;\n    display: flex;\n    align-items: center;\n    left: 0rem;\n}\n\n.budget-results-transactions span {\n    text-align: right;\n    width: 5rem;\n    font-size: 1.5rem;\n    padding-bottom: 0.05rem;\n}\n\n.budget-results-transactions-icon {\n    display: inline-block;\n    fill: var(--color-fill-icon);\n    width: 2rem;\n    height: 2rem;\n    margin-left: 0.2rem;\n}\n\n.budget-results-used {\n    font-size: 1.9rem;\n}\n\n.budget-results-used > span {\n    display: inline-block;\n    width: 6rem;\n}\n\n.budget-results-used-comment {\n    position: absolute;\n    right: 0rem;\n    font-size: 1.6rem;\n    color: var(--color-primary-deselected);\n}\n\n/* Custom components */\n\n#app {\n    width: 100vw;\n\n    /* The --vh CSS variable is set to the viewport height,\n       _without_ the browser navbar (important for mobile). */\n    height: 100vh; /* Fallback for browsers that do not support Custom Properties */\n    height: calc(var(--vh, 1vh) * 100);\n}\n\n.option-selection-name {\n    max-width: calc(100% - 2rem);\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.option-selection-options {\n    box-sizing: content-box;\n    z-index: 10;\n\n    left: -0.1rem;\n    right: -0.1rem;\n    background-color: var(--color-background-nav);\n    border: 0.15rem solid var(--color-primary-deselected);\n\n    overflow-y: auto;\n    max-height: 50vh;\n}\n\n.option-selection-options.drop-down {\n    position: absolute;\n    top: 2em;\n    padding-top: 0.7rem;\n    padding-bottom: 0rem;\n    border-top: none;\n}\n\n.option-selection-options.drop-up {\n    position: absolute;\n    bottom: 2em;\n    padding-top: 0rem;\n    padding-bottom: 0.7rem;\n    border-bottom: none;\n}\n\n.option-selection-options span {\n    display: block;\n    width: calc(100% - 1.7rem);\n    padding: 0.5rem 0.7rem 0.5rem 1rem;\n    line-height: 2rem;\n    text-align: left;\n    text-overflow: ellipsis;\n    overflow: hidden;\n    color: var(--color-primary-deselected);\n}\n\n.option-selection-options span:hover {\n    color: var(--color-primary-selected);\n    background: var(--color-background-light-hover);\n}\n\n.period-selection-year {\n    min-width: 12.3rem;\n    max-width: 12.3rem;\n}\n\n/* Utility classes */\n\n.full-size {\n    width: 100%;\n    height: 100%;\n}\n\n.position-relative {\n    position: relative;\n}\n\n.margin-top-10 {\n    margin-top: 10rem;\n}\n\n.margin-top-5 {\n    margin-top: 5rem;\n}\n\n.margin-top-small {\n    margin-top: 0.5rem;\n}\n\n.margin-bottom-small {\n    margin-bottom: 0.5rem;\n}\n\n.margin-left-small {\n    margin-left: 0.5rem;\n}\n\n.margin-right-small {\n    margin-left: 0.5rem;\n}\n\n.button-small {\n    font-size: 0.8rem;\n    height: 2.8rem;\n    line-height: 2.8rem;\n    padding: 0 1.5rem;\n}\n\n.text-center {\n    text-align: center;\n}\n\n.text-right {\n    text-align: right;\n}\n\n.flex-center {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.flex-space-between {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.flex-item-full {\n    flex: 1 1 auto;\n}\n\n.disabled {\n    display: none;\n}\n\n.inactive {\n    filter: brightness(55%);\n    pointer-events: none;\n}\n\n.invisible {\n    visibility: hidden;\n}\n\n.float-right {\n    float: right;\n}\n\n.icon-cross {\n    color: #bb2244;\n}\n\n.icon-ack {\n    color: #229944;\n}\n\n.color-positive {\n    color: var(--color-theme-positive);\n}\n\n.color-negative {\n    color: var(--color-theme-negative);\n}\n\n.clickable {\n    cursor: pointer;\n}\n\n/* Override Milligram defaults */\n.container {\n    max-width: 75rem;\n}\n\n/* Hide number input arrow buttons */\n/* Chrome, Safari, Edge, Opera */\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n/* Firefox */\ninput[type=\"number\"] {\n    -moz-appearance: textfield;\n}\n\n/* Override input text color to make it readable in dark-mode browsers. */\ninput[type=\"color\"],\ninput[type=\"date\"],\ninput[type=\"datetime\"],\ninput[type=\"datetime-local\"],\ninput[type=\"email\"],\ninput[type=\"month\"],\ninput[type=\"number\"],\ninput[type=\"password\"],\ninput[type=\"search\"],\ninput[type=\"tel\"],\ninput[type=\"text\"],\ninput[type=\"url\"],\ninput[type=\"week\"],\ninput:not([type]),\ntextarea,\nselect {\n    color: var(--color-primary);\n    border-color: var(--color-primary);\n}\n\ninput[type=\"color\"]:focus,\ninput[type=\"date\"]:focus,\ninput[type=\"datetime\"]:focus,\ninput[type=\"datetime-local\"]:focus,\ninput[type=\"email\"]:focus,\ninput[type=\"month\"]:focus,\ninput[type=\"number\"]:focus,\ninput[type=\"password\"]:focus,\ninput[type=\"search\"]:focus,\ninput[type=\"tel\"]:focus,\ninput[type=\"text\"]:focus,\ninput[type=\"url\"]:focus,\ninput[type=\"week\"]:focus,\ninput:not([type]):focus,\ntextarea:focus,\nselect:focus {\n    border-color: var(--color-theme);\n}\n";